:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
  --main-text: #000;
  --light-text: #fff;
  --main-background: #fff;
  --dark-background: #000;
  --brand-color-blue: #c1cbe7;
  --brand-color-pink: #fcd0ba;
}

html {
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  font-weight: 400;
}

*, :before, :after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

ol {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  margin: 0;
  padding: 0;
}

textarea {
  resize: none;
}

body {
  color: var(--main-text);
  background-color: var(--main-background);
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.animate__animated.animate__fadeIn {
  animation-duration: 2s;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 22px;
  padding-right: 22px;
}

.tagline {
  background-color: var(--brand-color-blue);
  width: 100%;
}

.tagline-box {
  text-align: center;
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 12px;
  line-height: 1.5;
  display: flex;
}

.mobile-menu-open {
  width: 32px;
  height: 32px;
  background-color: var(--brand-color-blue);
  border: 0;
  margin-left: auto;
  padding: 0;
}

.burger-icon {
  height: 32px;
  width: 32px;
  fill: var(--dark-background);
}

.navigation-list {
  display: none;
}

.mobile-menu-container {
  padding: 40px;
  position: relative;
}

.mobile-menu {
  z-index: 999;
  width: 100vw;
  height: 100vh;
  background-color: var(--main-background);
  flex-direction: column;
  gap: 8px;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.mobile-menu.is-open {
  transform: translateX(0);
}

.mobile-menu-close {
  width: 24px;
  height: 24px;
  background-color: var(--brand-color-blue);
  cursor: pointer;
  transition: background-color var(--timing-function) var(--animation-duration), fill var(--timing-function) var(--animation-duration);
  border: none;
  border-radius: 50%;
  margin-left: auto;
  padding: 4px;
}

.mobile-menu-close:hover, .mobile-menu-close:focus {
  background-color: var(--dark-background);
  fill: #fff;
}

.close-icon {
  width: 8px;
  height: 8px;
}

.mobile-navigation-item:not(:last-child) {
  margin-bottom: 16px;
}

.mobile-navigation-link {
  letter-spacing: .02em;
  text-transform: capitalize;
  transition: color var(--timing-function) var(--animation-duration);
  font-size: 36px;
  line-height: 1.22;
}

.mobile-navigation-link:hover, .mobile-navigation-link:focus, .mobile-navigation-link:active {
  color: var(--brand-color-blue);
}

.mobile-sl-list {
  justify-content: space-around;
  margin-top: auto;
  display: flex;
}

.mobile-sl-icon {
  width: 24px;
  height: 24px;
}

.mobile-sl-link {
  fill: var(--dark-background);
  transition: fill var(--timing-function) var(--animation-duration);
}

.mobile-sl-link:hover, .mobile-sl-link:focus {
  fill: var(--brand-color-blue);
}

.hero {
  text-align: center;
  padding-top: 36px;
  padding-bottom: 36px;
}

.hero-title {
  margin-bottom: 32px;
  font-size: 36px;
  line-height: 1.1;
}

.hero-subtitle {
  margin-bottom: 32px;
  font-size: 20px;
  line-height: 1.5;
}

.form-email {
  justify-content: center;
  margin-bottom: 32px;
  display: flex;
}

.form-input {
  border: 1px solid var(--brand-color-blue);
  width: 220px;
  height: 60px;
  border-radius: 50px 0 0 50px;
  padding: 15px;
  font-size: 20px;
  line-height: 1.5;
}

.form-button {
  background-color: var(--dark-background);
  height: 60px;
  color: var(--light-text);
  transition: background-color var(--timing-function) var(--animation-duration), color var(--timing-function) var(--animation-duration);
  border-radius: 0 50px 50px 0;
  padding: 8px;
}

.form-button:hover, .form-button:focus {
  background-color: var(--brand-color-blue);
  color: var(--main-text);
}

.hero-text {
  width: 278px;
  text-align: center;
  padding-bottom: 32px;
  padding-left: 16px;
  padding-right: 16px;
  font-size: 18px;
  line-height: 1.22;
}

.swiper-scrollbar {
  background-color: var(--brand-color-pink);
  opacity: 1;
}

.swiper-scrollbar-drag {
  background-color: var(--dark-background);
  opacity: 1;
}

.hero-note {
  margin-top: 32px;
  font-size: 10px;
  line-height: 1.22;
}

.hero-img {
  display: none;
}

.advantages, .app {
  background-color: var(--dark-background);
  padding: 36px 0;
}

.advantages-text-box {
  color: var(--light-text);
  text-align: center;
}

.advantages-subtitle {
  text-transform: uppercase;
  margin-top: 36px;
  font-size: 20px;
  line-height: 1.4;
}

.advantages-text {
  margin-top: 18px;
  font-size: 36px;
  line-height: 1.14;
}

.advantages-img {
  margin-left: auto;
  margin-right: auto;
}

.first-step, .second-step {
  padding: 36px 10px;
}

.step-title {
  text-transform: uppercase;
  text-align: center;
  font-size: 20px;
  line-height: 1.4;
}

.step-subtitle {
  text-align: center;
  margin-top: 30px;
  font-size: 36px;
  line-height: 1.2;
}

.first-step-box, .second-step-box {
  border-radius: 12px;
  margin-top: 36px;
  margin-left: auto;
  margin-right: auto;
}

.first-step-box {
  background-color: var(--brand-color-pink);
  padding: 20px 12px;
}

.second-step-box {
  background-color: var(--brand-color-blue);
  padding: 20px 12px;
}

.last-box {
  padding: 20px 12px;
}

.step-text-subtitle {
  text-transform: uppercase;
  margin-bottom: 16px;
  font-size: 18px;
  line-height: 1.4;
}

.step-text-title {
  margin-bottom: 16px;
  font-size: 36px;
  line-height: 1.07;
}

.step-text-list {
  flex-direction: column;
  gap: 20px;
  font-size: 16px;
  line-height: 2;
  display: flex;
}

.step-text-item {
  min-height: 40px;
  background-image: url("tick.47843b8f.png");
  background-repeat: no-repeat;
  padding-left: 60px;
}

.step-text-note, .phone-img, .phone-img-2, .card-img {
  display: none;
}

.step-button {
  background-color: var(--dark-background);
  color: var(--light-text);
  text-align: center;
  text-transform: uppercase;
  transition: background-color var(--timing-function) var(--animation-duration), color var(--timing-function) var(--animation-duration);
  border-radius: 50px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px 35px;
  font-size: 16px;
  line-height: 1.5;
  display: block;
}

.step-button:hover, .step-button:focus {
  background-color: var(--main-background);
  color: var(--main-text);
}

.account-text {
  font-size: 16px;
  line-height: 1.2;
}

.account-img {
  margin-left: auto;
  margin-right: auto;
}

.app-text-box {
  color: var(--light-text);
  text-align: center;
  margin-top: 16px;
}

.app-item {
  margin-bottom: 20px;
}

.app-subtitle {
  text-transform: uppercase;
  margin-bottom: 8px;
  font-size: 14px;
  line-height: 1.4;
}

.app-text {
  font-size: 12px;
  line-height: 1.5;
}

.app-img {
  margin-left: auto;
  margin-right: auto;
}

.applications-box {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.applications-box-item {
  background-color: var(--main-background);
  color: var(--main-text);
  transition: transform var(--timing-function) var(--animation-duration);
  border-radius: 12px;
  padding: 20px 32px 30px;
}

.applications-box-item:hover, .applications-box-item:focus {
  transform: scale(1.25);
}

.application-image {
  margin-bottom: 8px;
  margin-left: auto;
  margin-right: auto;
}

.rate {
  font-size: 35px;
  line-height: 1.1;
}

.reviews {
  color: gray;
  margin-bottom: .8px;
  font-size: 12px;
  line-height: 1.5;
}

.feedback {
  padding: 36px 10px;
}

.feedback-subtitle {
  text-align: center;
  margin-bottom: 20px;
  font-size: 36px;
  line-height: 1.2;
}

.feedback-item {
  width: 260px;
  height: 220px;
  text-align: center;
  background-color: #efefef;
  border-radius: 12px;
  position: relative;
}

.feedback-text-box {
  width: 240px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.feedback-text {
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 1.14;
}

.feedback-name {
  font-size: 12px;
  line-height: 1.5;
}

.swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  text-align: center;
  opacity: 1;
  background-color: var(--brand-color-blue);
}

.swiper-pagination-bullet-active {
  background-color: var(--dark-background);
}

.contacts {
  background-color: var(--brand-color-pink);
  text-align: center;
  padding: 36px 10px;
}

.contacts-subtitle {
  margin-bottom: 16px;
  font-size: 36px;
  line-height: 1.14;
}

.contacts-address {
  font-style: normal;
}

.contacts-address-subtitle {
  margin-bottom: 16px;
  font-size: 20px;
  line-height: 1.14;
}

.contacts-address-button {
  background-color: var(--main-background);
  transition: color var(--timing-function) var(--animation-duration), background-color var(--timing-function) var(--animation-duration);
  border-radius: 50%;
  margin-bottom: 16px;
  padding: 16px;
  font-size: 18px;
  line-height: 1.4;
}

.contacts-address-button:hover, .contacts-address-button:focus {
  background-color: var(--dark-background);
  color: var(--light-text);
}

.contacts-text {
  font-size: 16px;
  line-height: 1.4;
}

.conclusion-img {
  display: none;
}

.footer {
  background-color: var(--dark-background);
  color: var(--light-text);
  text-align: center;
  padding: 36px 10px;
}

.footer-note {
  color: #b3b3b3;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 1.5;
}

.footer-links {
  font-size: 16px;
  line-height: 1.5;
}

.footer-navigation-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px 36px;
  margin-bottom: 36px;
  display: flex;
}

.footer-navigation-link {
  transition: color var(--timing-function) var(--animation-duration);
}

.footer-navigation-link:hover, .footer-navigation-link:focus {
  color: var(--brand-color-blue);
}

.footer-sl-list {
  justify-content: center;
  gap: 36px;
  display: flex;
}

.footer-sl-icon {
  width: 24px;
  height: 24px;
}

.footer-sl-link {
  fill: var(--light-text);
  transition: fill var(--timing-function) var(--animation-duration);
}

.footer-sl-link:hover, .footer-sl-link:focus {
  fill: var(--brand-color-pink);
}

.thrive, .card {
  padding: 36px 10px;
}

.thrive-title, .card-title {
  margin-bottom: 16px;
  font-size: 24px;
  line-height: 1.07;
}

.thrive-text, .card-text {
  font-size: 16px;
  line-height: 1.2;
}

.thrive-text:not(:last-child) {
  margin-bottom: 24px;
}

.card-subtitle {
  margin-top: 16px;
  margin-bottom: 16px;
  font-size: 20px;
  line-height: 1.6;
}

.card-list-item {
  margin-bottom: 8px;
  font-size: 16px;
  line-height: 1.2;
}

.card-list-item:last-child {
  margin-bottom: 0;
}

